import React from 'react';

const PdfDocumentCard = ({ pdf, onSelect, onRemove, isSelected }) => {
  return (
    <div
      className={`relative p-4 bg-white rounded-lg shadow-md cursor-pointer transition-all duration-300 ease-in-out transform hover:scale-105 ${
        isSelected ? 'border-4 border-blue-500' : 'border-4 border-transparent'
      }`}
      onClick={() => onSelect(pdf.id)}
    >
      {/* Nombre del archivo */}
      <h3
        className="text-lg font-semibold text-gray-800 truncate mb-2"
        title={pdf.name}
      >
        {pdf.name}
      </h3>

      {/* Ícono PDF */}
      <div className="w-full h-32 flex items-center justify-center rounded-md bg-red-50">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="w-12 h-12 text-red-600"
          viewBox="0 0 24 24"
          fill="currentColor"
        >
          <path d="M14 2H6a2 2 0 00-2 2v16a2 2 0 002 2h12a2 2 0 002-2V8z" />
          <path
            fillRule="evenodd"
            d="M14 2v6h6"
            clipRule="evenodd"
            className="text-red-400"
          />
          <text
            x="12"
            y="17"
            textAnchor="middle"
            fontSize="6"
            fill="currentColor"
            className="font-bold"
          >
            PDF
          </text>
        </svg>
      </div>

      {/* Botón eliminar */}
      <button
        className="absolute top-2 right-2 bg-red-500 text-white rounded-full p-1 hover:bg-red-600 transition-colors duration-200"
        onClick={(e) => {
          e.stopPropagation(); // evita que se dispare el onSelect al eliminar
          onRemove(pdf.id);
        }}
        title="Eliminar PDF"
      >
        <svg
          className="w-5 h-5"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="2"
            d="M6 18L18 6M6 6l12 12"
          />
        </svg>
      </button>
    </div>
  );
};

export default PdfDocumentCard;
